        "title": "За последний год в России продолжился рост игровой индустрии",
        "previewURL": "id2p.jpg",
        "date": "30.11.2022",
        "content": "<p>Аналитики Rambler&Co, ЮMoney и «Чек Индекс» проанализировали успехи игровой индустрии в России за прошедший год.</p><p>Согласно их информации, в отрасли за год наблюдался рост, а выводы следующие (в опросе участвовало 300 тысяч пользователей):</p><ol><li>За 10 месяцев 2022 года игроки совершили на 58% больше покупок через онлайн-сервисы, чем за 2021-й. Сами обороты площадок выросли на 63%, а средний чек увеличился на 3% (до 488 рублей). Платёжеспособная аудитория выросла на 77%, чаще всего товары оплачиваются банковскими картами (55%);</li><li>Объёмы продаж игровых PC увеличились на 32%, но у консолей снизились на 30%;</li><li>Примерно 81% пользователей тратит до тысячи рублей на игры в месяц, 13% — от тысячи до пяти тысяч рублей. От пяти до десяти тысяч готовы тратить 2%, а 4% могут позволить покупки на сумму более десяти тысяч рублей за месяц;</li><li>У 75% населения установлено хотя бы одно игровое мобильное приложение;</li><li>Среди опрошенных 39% играют на PC, на смартфонах — 24%. PlayStation собрала 16%, Xbox — 12%, а Nintendo — 6%. Остальные предпочитают облачные сервисы.</li><li>При этом 77% предпочитают играть в одиночку, тогда как 23% выбирают мультиплеерные проекты;</li><li>Среди популярных жанров оказались симуляторы (24%), шутеры (23%), RPG (13%) и стратегии (13%). У квестов 12%, экшены собрали лишь 8%, а у MMO с файтингами и того меньше — по 3%. При этом в пользу MOBA и вовсе высказался лишь 1% опрошенных;</li><li>Порядка 12% опрошенных покупает игры каждую неделю, 13% — раз в месяц, 11% — несколько раз в месяц. Тратиться на игры раз в несколько месяцев готовы 16% (столько же и в год). Остальные 32% отметили, что не покупают новые игры;</li><li>По часу в день на игры тратит 34%, а 29% запускают игры только несколько раз в неделю (раз в месяц 14%, а в год — 23%). Большая часть аудитории предпочитает играть вечером (65%), у любителей ночных посиделок 14%, а утренних — 8%.</li></ol><p>Кроме того, в отчёте отмечается, что больше стали зарабатывать и стримеры — оборот на платформе ЮStream вырос на 20% по отношению к 2021 году и составил почти 39 млн рублей. Сумма среднего перевода от пользователя стримерам выросла на 3% до 341 рубля.</p><p>Кроме того, пользователи стали чаще покупать клавиатуры (спрос на 32% выше) и игровые мышки (рост в 45%). Больше трети покупок электроники пришлось на первый квартал года. Но с прошлого октября, правда, наблюдается новое падение продаж.</p>"
