        "date": "30.11.2022",
        "content": "<p>Как оказалось, в разработке хоррора The Callisto Protocol помогало порядка 150 сотрудников PlayStation Studios Visual Arts.</p><p>Всё это обнаружили благодаря недавней утечке по игре, в рамках которой в сети появилось полное прохождение новинки. И одна из секций в заключительных титрах как раз была посвящена работникам студии PlayStation.</p><p>Ранее о поддержке Visual Arts упоминал её бывший руководитель Майкл Мумбауэр, который отметил, что сотрудничество не ограничивалось использованием создателями хоррора студией по захвату движений. Представители PlayStation помогали с кинематографическими роликами и другими элементами игры.</p><p>Кроме того, недавно о работе над проектом поведала звезда сериала «Пацаны» Карен Фукухара, а эта роль стала для неё первой в мире видеоигр. При этом релиз самой The Callisto Protocol состоится на PC, PlayStation и Xbox уже совсем скоро — 2 декабря.</p>"
